import React, { Component } from 'react';
import { NavLink } from "react-router-dom";

class Right extends Component {

    render() {
        return (

            <div className="right">
                <aside>

                    <p>:</p>
                    <p>
                        <ul>
                            <li><NavLink to="/admin/add_person/">  </NavLink></li>
                            <li><NavLink to="/admin/add_dep/">  </NavLink></li>
                            <li><NavLink to="/admin/add_place/">   </NavLink></li>
                            <li><NavLink to="/admin/add_addr/">  </NavLink></li>
                            <li><NavLink to="/admin/add_rank/">  </NavLink></li>
                            <li><NavLink to="/admin/add_pos/">  </NavLink></li>
                        </ul>
                    </p>

                    <p>:</p>
                    <p>
                        <ul>
                            <li><NavLink to="/admin/pos/">  </NavLink></li>
                            <li><NavLink to="/admin/rank/">  </NavLink></li>
                            <li><NavLink to="/admin/addr/">  </NavLink></li>
                            <li><NavLink to="/admin/dep/">  </NavLink></li>
                            <li><NavLink to="/admin/dismissed">  </NavLink></li>
                        </ul>
                    </p>

                </aside>
            </div>

        );
    }
}

export default Right;